$font-stack: 'GothamPro', sans-serif;

@font-face {
  font-family: 'GothamPro';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro.ttf);
}

@font-face {
  font-family: 'GothamPro-Light';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Light.ttf);
}

@font-face {
  font-family: 'GothamPro-Black';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Black.ttf);
}

@font-face {
  font-family: 'GothamPro-Medium';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Medium.ttf);
}

$font : 'GothamPro';
$fontLight : 'GothamPro-Light';
$fontBlack : 'GothamPro-Black';
$fontMedium : 'GothamPro-Medium';


.none {
  padding: 0;
  margin: 0;
}

html {
  min-width: 1240px !important;
  font-size: 18px;
}

.container {
  width: 1240px !important;
}

body {
  font-family: $font-stack;
  font-size: 18px;
  min-width: 1240px !important;
  color: #383838;
}

.menuBlock {
  width: 50%;
  min-height: 350px;
  height: 100%;
  max-height: 1100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9000;
  overflow: hidden;

  #menuHumburger {
    width:25px;
    height:25px;
    background-image:url(../img/sprite_x25.png);
    background-position:0% 0%;
    cursor: pointer;
    display: inline-block;
    position: absolute;
    top: 2.5vw;
    right: 57%;
    z-index: 9999;
  }

  .backgroundCircle {
      background-color: #c8ae76;
      width: 25px;
      height: 25px;
      border-radius: 2500px;
      right: 57%;
      position: absolute;
      top: 33px;
      display: none;
  }
  ul {
    display: none;
    position: absolute;
    letter-spacing: 0.03rem;
    z-index: 9999;
    right: 12%;
    top: 14.2vw;
    font-size: 1vw;
    list-style: none;
    color: #fff;
    font-family: $fontMedium;
    margin: 0;
    padding: 0;
    width: 39%;
    li {
      margin-bottom: 0.79vw;
      a {
        color: #fff;
        &:hover {
          color: #fff;
        }
      }
    }
    #separator {
      background-color: #e1e1e1;
      height: 1px;
      width: 100%;
      margin-top: 4.2vw;
      margin-bottom: 1.7vw;
    }
  }
}

header {
  position: relative;
  height: 56.3vw;
  h1 {
    position: absolute;
    top: 2.2vw;
    left: 8.5%;
    letter-spacing: 0.04rem;
    font-size: 2vw;
    font-family: GothamPro-Light;
  }

  h2 {
    position: absolute;
    top: 22vw;
    left: 9%;
    letter-spacing: 0.3rem;
    font-size: 3vw;
    text-transform: uppercase;
    span {
      display: block;
      &:first-child {
        font-family: $fontLight;
      }
      &:last-child {
        font-family: $font;
        letter-spacing: 0.4rem;
      }
    }
  }
  img {
    position: absolute;
    top: 0;
    /*left: 65px;
    width: 555px;*/

    left: 8.8vw;
    width: 82%;
    z-index: -9999;
  }

  .socialBlock {
    position: absolute;
    top: 2.5vw;
    left: 57%;
    height: auto;
    z-index: 1;
    width: auto;
    a {
      img {
      width: 1.8vw;
      height: auto;
      z-index: 0;
      position: relative;
      left: 0;
      }
    }
  }
  .arrowNext {
    position: absolute;
    z-index: 9999;
    bottom: 1.7vw;
    margin-left: -1.5%;
    left: 0;
    height: auto;
    width: 3%;
    img {
      width: 100%;
      position: relative;
      z-index: 9999;
      left: 0;
    }
  }
  .row, .col-sm-6 {
    height: 100%;
  }
}

.button {
  position: absolute;
  top: 33.4vw;
  left: 9%;
  letter-spacing: 0.089rem;
  font-size: 0.74vw;
  border: 1px solid #000;
  text-transform: uppercase;
  padding: 0.8vw 7.5% 0.8vw 8.3%;
  display: block;
  color: #000;
    &:hover, &:active {
      color: #000;
    }
}

.pagesIndex {
  width: 22%;
  position: fixed;
  right: 0;
  top: 19vw;
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 1vw;
      margin-top: 1.5vw;
      margin-left: 5.4%;
      &:first-child {
          margin-top: 0;
      }
      a {
        color: #7d7d7d;
        &:hover, &:active {
          color: #7d7d7d;
          text-decoration: none;
        }
        p {
          display: inline-block;
          font-size: 1.5vw;
          margin: 0;
        }
        span {
          width: 79%;
          height: 2px;
          background-color: #444;
          display: none;
          vertical-align: top;
          margin-top: 1.7vw;
          margin-left: 7.5%;

        }
      }
    }
    .active {
      margin-left: 0;
      a {
        color: #444;
        &:hover, &:active {
          color: #444;
          text-decoration: none;
        }
      }
      p {
      font-size: 2vw;
      }
      span {
        display: inline-block;
      }
    }
  }
}
